










































import Vue from 'vue'
import axios from 'axios'
import Loader from '@/components/Loader.vue'

export default Vue.extend({
    name: "Projects",
    components: {
      Loader
    },
    data: function(){
        return{        
          OnLoad:true,
          ProjectsData: null,
          lan: null,
          ActiveTags: []
        }

    },
    computed:{
      PageData(){
        if(this.lan == 'ru'){
          return {
            Header: 'Мои проекты',
            Details: 'Подробнее...'
          }
        }
        else{
          return {
            Header: 'My projects',
            Details: 'More details...'
          }
        }
      },
      UniqueTags(){
        const result = [];

        this.ProjectsData.forEach(project => {
          project.HashTags.forEach(tag => {
            if (!result.includes(tag)) {
              result.push(tag);
            }
          });
        });

        return result;
      }
    },
    watch:{
      $route() {this.FetchData();}
    },
    mounted(){
      this.FetchData();
    },
    methods:{
      ActiveProject(projectTags){
        let result = false;
        if (this.ActiveTags.length == 0)
          result = true;
        else{
          projectTags.forEach(tag => {
            if (this.ActiveTags.includes(tag))
              {
                result = true;
              }
          });
          this.ActiveTags.forEach(tag => {
            if (!projectTags.includes(tag))
              {
                result = false;
              }
          });
        }
        return result;
      },
      InsertTag(tag, event){
        if (this.ActiveTags.includes(tag)){
            this.ActiveTags.splice(this.ActiveTags.indexOf(tag), 1);
            event.target.parentElement.classList.remove('Active');
          }
        else {
          event.target.parentElement.classList.add('Active');
          this.ActiveTags.push(tag);
        }
      },
      FetchData(){
        this.OnLoad=true;
        axios
        .get(this.$data.ServerLink +'/get-projects?language=' + this.$route.params.lan + '&prof=' + this.$route.params.prof)
        .then(response => {
          if (response.data.status=="error")
          console.log(response.data.message);
          else{
            if(response.data.status=="not found")
              this.$router.push({name:'Error404', params:{ lan:this.$route.params.lan }});
            else
              this.ProjectsData = response.data;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(()=>{this.OnLoad=false;})
        this.lan = this.$route.params.lan;
      }
  } 
})
