






































import Vue from 'vue'
import axios from 'axios'
import Loader from '@/components/Loader.vue'
export default Vue.extend({
    name:'Prices',
    components:{
      Loader
    },
    data(){
      return{
        OnLoad:true,
        PricesPageData: {
              language:'',
              Img1:{
                Layer1:'',
                Layer2:'',
                Layer3:'',
                Layer4:'',
              },
              Header1:'',
              Text1:'',
              Text2:'',
              PriceCards:[
                {
                  id:'',
                  name:'',
                  Img:'',
                  price:'',
                  description:''
                }
              ]
            }
      }
    },
    methods:{
      ShowCard(event){
          if (event.target.parentElement.classList.contains('Active') )
            event.target.parentElement.classList.remove('Active');
           else {
             event.target.parentElement.classList.add('Active');
             event.target.parentElement.classList.remove('nonActive');
           }
        event.target.parentElement.classList.add('activate');
        setTimeout(function(){event.target.parentElement.classList.remove('activate')}, 300);
      },
      FetchData(){
        this.OnLoad=true;
        axios
        .get(this.$data.ServerLink +'/get-prices-page-data?language=' + this.$route.params.lan + '&prof=' + this.$route.params.prof)
        .then(response => {
          if (response.data.status=="error")
          console.log(response.data.message);
          else{
            if(response.data.status=="not found")
              this.$router.push({name:'Error404', params:{ lan:this.$route.params.lan }});
            else
              this.PricesPageData = response.data;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(()=>{this.OnLoad=false;})
      }
    },
    watch:{
      $route() {this.FetchData();}
    },
    created(){ this.FetchData(); },

})
